.wj-hidden {
    &.smm-submenu-toggler {
        +ul {
            display: none;
        }
    }
}

.wj-form-wrapper {
    padding: 20px;
    max-width: 810px;
    width: 100vw;
}

.wj-form__field {
    margin-bottom: 0;
    label {
        // color: @color-resolution-blue;
        // color: @color-white;
        font-weight: @weight-semibold;
        margin-bottom: 7px;
    }
    input {
        padding: 14px 13px 15px 13px;
        font-size: 1.4rem;
        width: 300px;
    }
}

.form-common-on-page {
    width: 100%;
    max-width: none;
    background-color: @color-transparent;
    .wj-form__field {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(25, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        input {
            width: 100%;
            border: 1px solid @color-gray-light;
        }
    }
    .wj-form__field--radio {
        max-width: 250px;
        input {
            display: inline-block;
            width: auto;
        }
        label {
            color: @default-color-text;
            display: inline-block;
        }
    }
    label {
        font-weight: @weight-normal;
        color: @color-cerulean;
        &[for="applicant-email"] {
            position: relative;
            .wj-icon-cm-email {
                position: absolute;
                top: 31px;
                left: 10px;
                font-size: 1.6em;
            }
            input {
                padding-left: 40px;
            }
        }
    }
    select {
        border: 1px solid @color-gray-light;
       padding: 14px 13px 13px 13px;
    }
    .wfsg-spinner {
        display: none;
    }
}

.wj-w-100 {
    width: 100%;
}

.wj-tac {
    text-align: center;
}

.wj-bg-gray-light {
    background-color: @color-gray-light;
}

.wj-bg-transparent {
    background-color: @color-transparent !important;
}

.wj-2cols {
    columns: 2;
    column-gap: 60px;
}

.wj-pad-20 {
    padding: 20px;
}

.wj-maw-50 {
    max-width: 50%;
}

.wj-center {
    margin: 0 auto;
}

.wj-flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.wj-flex-two {
    li {
        flex-basis: 48% !important;
    }
}

.wj-flex-four {
    li {
        flex-basis: 24% !important;
    }
}


.wj-research-link {
    // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(20, @screen-base-height);
    margin-bottom: @height-in-rem; // fallback for older browsers
    margin-bottom: @height-in-vh;
    padding: 2px;
    font-weight: @weight-bold;
    color: @color-white;
    background-color: lighten(@color-gold-drop, 15%);
    &:hover {
        color: @color-white;
        background-color: lighten(@color-gold-drop, 15%);
    }
}

.request-report {
    // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(30, @screen-base-height);
    margin: @height-in-rem auto 0 auto !important;
    margin: @height-in-vh auto 0 auto !important;
}

.wj-cookies-consent {
    z-index: @z-index-high;
    position: fixed;
    bottom: -100%;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    padding: 20px;
    text-align: center;
    background-color: @color-white;
    box-shadow: 0 -3px 10px @color-gray-light;
    // opacity: 0;
    // z-index: @z-index-minus;
    transition: bottom 0.3s 1s;
    &:hover {
            bottom: 0;
    }
    p {
        margin-bottom: 20px;
    }
}

.wj-cookies-consent-button {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 200px;
    &:hover {
        +.wj-cookies-consent {
            // opacity: 1;
            // z-index: @z-index-high;
            bottom: 0;
        }
    }
}
