.common-page-content {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: @color-white-dark;
}

.bpc-two-cols {
    display: flex;
    justify-content: space-between;
    .cpc-body {
        flex: 0 1 49.3%;
    }
}

.cpc-vacancy-page {
    .cpc-body-item {
        h5 {
            font-weight: @weight-medium;
        }
        h6 {
            color: @color-cerulean;
        }
        .wj-btn-standard {
            margin-top: 30px;
        }
    }
}

.cpc-body {
    flex: 0 1 (880 / 1185) * 100%;
    max-width: (880 / 1185) * 100%;
    h3 {
        .wj-pixeltovh(20, @screen-base-height);
        padding-bottom: @height-in-rem; // fallback for older browsers
        padding-bottom: @height-in-vh;
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh; //         text-transform: uppercase;
        font-weight: @weight-medium;
        border-bottom: 1px solid @color-gray-light;
        line-height: 1;
        span {
            display: block;
            color: @color-cerulean;
        }
    }
    h6 {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(25, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        line-height: 1;
        p {
            margin-bottom: 0;
        }
    }
    p,
    ul,
    table {
        +h6 {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(50, @screen-base-height);
            margin-top: @height-in-rem; // fallback for older browsers
            margin-top: @height-in-vh;
        }
    }
    h3,
    h4,
    h5,
    h6 {
        color: @color-resolution-blue; // converts px into vh: get the result in @height-in-vh
    }
    blockquote {
        margin-left: 0;
        margin-right: 0;
        font-style: @style-italic;
    }
    table {
        border-collapse: collapse;
    }
    thead {
        font-weight: @weight-medium;
        color: @color-cerulean;
    }
    td {
        padding: 5px 10px;
        border: 1px solid @color-gray-light;
    }
    p {
        +table,
        +div {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(20, @screen-base-height);
            margin-top: @height-in-rem; // fallback for older browsers
            margin-top: @height-in-vh;
        }
    }
    hr {
        border-top-style: none;
        border-color: @color-gray-light;
    }
    &__text {
        line-height: 22 / 16;
        p {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(10, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
        }
        h4 {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(10, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
            margin-top: @height-in-rem * 1.1; // fallback for older browsers
            margin-top: @height-in-vh * 1.1;
        }
    }
    &__text-more {
        display: none;
        &.wj-expanded {
            display: block;
        }
    }
    &__list {
        list-style-type: square;
        list-style-position: outside; // converts px into vh: get the result in @height-in-vh
        padding-left: 40px;
        .wj-pixeltovh(30, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
    }
    &__similar-vacancies {
        h5 {
            color: @color-resolution-blue; // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(20, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
        }
        h6 {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(15, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
            font-weight: @weight-normal;
        }
        li {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(20, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
            line-height: 1.4;
        }
    }
    &__hero-img-wrapper {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(15, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        img {
            width: 100%;
        }
        iframe {
            max-width: 836px;
            width: 100%;
        }
    }
    &__float-img-wrapper {
        float: left;
        margin-right: 20px;
    }
    &__blue-block {
        padding: 20px;
        background-color: @color-humming-bird;
    }
    &__person-block {
        text-align: center;
        h6 {
            font-size: 1.4rem;
            color: @default-color-text;
        }
        blockquote {
            font-size: 2rem;
            color: @color-resolution-blue;
        }
    }
    &__research-teaser {
        .bpc-posts-list {
            li {
                flex-basis: 48%;
                border-bottom: 1px solid @color-gray-light;
            }
        }
    }
    &__events-list {
        li {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(30, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;

            border-bottom: 1px solid @color-gray-light;
            h5 {
                margin-bottom: 10px;
            }
            h6 {
                font-size: 1.6rem;
                color: @default-color-text;
                margin-bottom: 10px;
            }
        }
    }
    &__bquote-borders {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        padding: @height-in-rem 0; // fallback for older browsers
        padding: @height-in-vh 0;
        margin-bottom: @height-in-rem / 20 * 55; // fallback for older browsers
        margin-bottom: @height-in-vh / 20 * 55;
        font-style: @style-normal !important;
        border-top: 1px solid @color-gray-light;
        border-bottom: 1px solid @color-gray-light;
        b {
            font-weight: @weight-bold;
            color: @color-resolution-blue;
        }
    }
    &__past-journal-issues {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        img {
            width: 170px;
        }
    }
    +.cpc-sidebar {
        margin-left: 20px;
    }
}

.cpc-body-item {
    padding: 20px; // converts px into vh: get the result in @height-in-vh
    background-color: @color-white;
    +.cpc-body-item {
        .wj-pixeltovh(20, @screen-base-height);
        margin-top: @height-in-rem; // fallback for older browsers
        margin-top: @height-in-vh;
    }
}

.cpc-sidebar {
    flex: 0 1 280px;
    max-width: (280 / 1185) * 100%;
    min-width: 280px;
    +.cpc-body {
        margin-left: 20px;
    }
}

.cpc-body-el__details {
    font-weight: @weight-bold;
    line-height: 0.9;
    span {
        font-weight: @weight-normal;
    }
}


@import url("../blocks/widget.less");
@import url("../blocks/widget-vacancy-details.less");
@import url("../blocks/services-list.less");
