.wj-contacts-tiles {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
        flex: 0 1 48%;
        padding: 20px;
        background-color: @color-white;

        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        
        
    }
}

.cpc-body-item--contacts {
	padding: 0;
	background-color: @color-transparent;
}
