// fixed popup center horizontally and vertically 
// includin centering content
.center-fixed-hv() {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

// center absolute vertically & horizontally
.center-absolute-hv() {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

// center absolute vertically
.center-absolute-v() {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

// center absolute horizontally
.center-absolute-h() {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
}


.flex-parent(@j-cont: @default-justify-content) {
    display: flex;
    flex-wrap: wrap;
    justify-content: @j-cont;
}

.wj-intellihide() {
    position: absolute !important;
    top: -9999px !important;
    left: -9999px !important;
}

// clears ul/li/a default behaviour
// should be used on upper nav element
.clear-list-nav-a() {
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            display: block;
            a {
                text-decoration: none;
            }
        }
    }
}

.clear-list-ul-a() {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
        display: block;
        a {
            text-decoration: none;
        }
    }
}

.wj-clearfix() {
    &:before,
    &:after {
        content: " "; // 1
        display: table; // 2
    }
    &:after {
        clear: both;
    }
}

// transparent color:
// how to: call the mixin in parent,
// get there the rgba color in the variable @color-rgba
.wj-transparent(@color: black; @alpha: 0.5) {
    @channel-red: red(@color);
    @channel-green: green(@color);
    @channel-blue: blue(@color);
    @color-rgba: rgba(@channel-red, @channel-green, @channel-blue, @alpha);
    // @channel-red: red;
}

// Responsive utility
// convert font size REM units into equal
// VW units using predefined base viewport width 
// Returns: @size-in-vw
.wj-remtovw(@size-in-rem: 1;
@def-viewport: @default-base-viewport) {
    @size-in-px: @size-in-rem * 10;
    @base-vw: @def-viewport / 100;
    @size-in-vw: unit(@size-in-px / @base-vw, vw);
}

// Responsive utility
// convert line-height from unitless to % of base font-size
// Returns: @lh-in-percent
.wj-lhtopercent(@lh-in-rem;
@base-font-size-in-rem) {
    @lh-in-percent: unit(@lh-in-rem / @base-font-size-in-rem) * 100%;
}

// Responsive utility
// Make a rectangle responsibe by converting
// the width & height from PX to VW dependiing on viweport size
// and the multiplier (the multiplier makes conversion effect smaller
// or bigger in direct relationship)
// Returns: @width-in-vw, @height-in-vw;
.wj-whtovw(@width-in-px;
@height-in-px;
@viewport-width-in-px;
@multi: 1) {
    @width-in-vw: @width-in-px / @viewport-width-in-px * 100vw * @multi;
    @height-in-vw: @height-in-px / @viewport-width-in-px * 100vw * @multi;
}

// Responsive utility pxtovh()
// Produces VH (! not vw) from PX
// to provide e.g. margin-bottom with VH-dependent value
// to make vertical settings responsive
// Returns: @height-in-vh, height in rem (fallback)
.wj-pixeltovh(@height-in-px;
@viewport-height-in-px) {
    @height-in-rem: @height-in-px / 10 * 1rem;
    @height-in-vh: @height-in-px / @viewport-height-in-px * 100vh;
}

// Responsive utility pixeltoem()
// Produces em from px for margin-bottom or padding-bottom
// to depend on the element's font size & so be responsive.
// Appropriate for smaller vertical betwen element, where the 
// spaces which do not tend to depen on verical size of the 
// parent block, but more dependent on element's itself 
// font size.
// @Params: @size-in-px - size that should be converted to em.
//          @base-font-size-in-rem - font size relative to which
//          px will be recalculated to em.
// @Returns: @size-in-em
.wj-pixeltoem(@size-in-px;
@base-font-size-in-rem) {
    @size-in-em: ((@size-in-px / 10) / @base-font-size-in-rem) * 1em;
}

// Responsive utility padtopercent()
// NB! Not applicable to fixed elements
// as they measure % padding of the parent 
// Produces padding rule in % using @base-width.
// @Params: @px-X - size that should be converted to %.
//          @base-width - width of the element which
//          the padding should be.
// @Returns: line 'padding: X1 X2 X3 X4'
.wj-padtopercent(@px-t; @px-r; @px-b; @px-l; @base-width) {
    @percent-t: @px-t / @base-width * 100%;
    @percent-r: @px-r / @base-width * 100%;
    @percent-b: @px-b / @base-width * 100%;
    @percent-l: @px-l / @base-width * 100%;
    padding: @percent-t @percent-r @percent-b @percent-l;
}
