// Optimization stage custom styles
.bg-setup {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
}

.b-lazy.b-loaded {
    filter: none;
    opacity: 1;
}

