.scv-control-bar {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 20px;
  
    &__reset-results {
        min-width: 255px;
    }
    &__filters {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        max-width: 906px;
        background-color: @color-cerulean;
        >li {
            position: relative;
        }
        .wj-icon-cm-arrow-down {
            &:before {
                position: absolute;
                font-size: 1.6em;
                top: 7px;
                right:0;
            }
        }
    }
    .wj-btn-standard {
          font-size: 1.4rem;
          font-weight: @weight-medium;
        &:not(.scv-control-bar__reset-results) {
            &:hover,&:active,&:focus {
                background-color: @color-cerulean;
            }
        }
    }
}

[class*="svc-cb-menu__"] {
    position: absolute;
    z-index: @z-index-minus;
    top: 100%;
    color: @default-color-text;
    background-color: @color-white;
    box-shadow: 0 3px 15px @color-gray;
    opacity: 0;
    transition: opacity 0.3s;
    &:hover {
        z-index: @z-index-mid;
        opacity: 1;
    }
    li {
        padding: 10px;
        white-space: nowrap;
    }
    a {
        display: block;
        color: inherit;
    }
}

.svc-cb-menu__vt {
    width: 156px;
}


.scv-control-bar__filters-vacancy-type,
.scv-control-bar__filters-sort-by {
    right: 0;
}

.scv-control-bar__filters-payment {
    ul {
        left: 40px;
        li {
            font-size: 1.4rem;
            p {
                font-size: 1.1rem;
                text-transform: uppercase;
                margin-bottom: 5px;
            }
            select {
                background-color: @color-white-dark;
                padding: 10px;
                border: 0;
            }
        }
    }
    .wj-btn-standard {
        padding-left: 40px;
        &.wj-icon-cm-adjust {
            &:before {
                font-size: 2.6rem;
                position: absolute;
                transform: rotate(90deg);
                top: 7px;
                left: 5px;
            }
        }
    }
}

.svc-cb-payment-type {
    width: 100%;
}

.svc-cb-fp {
    width: 100%;
    padding-left: 30px;
    background-color: @color-resolution-blue;
    &:hover {
        background-color: @color-cerulean;
    }
}

.scv-control-bar__filters-sort-by {
    margin-right: 0;
    margin-left: auto;
    ul {
        right: 0;
    }
}

.scv-control-bar__filters {
    .scv-control-bar__filters-payment>ul {
        // z-index: @z-index-mid;
        // opacity: 1;
    }
    .wj-btn-standard {
        &:before {
            margin-right: 10px;
        }
        &:hover {
            +ul {
                z-index: @z-index-mid;
                opacity: 1;
            }
        }
    }
}

.svc-cb-menu__vt {
    // z-index: @z-index-mid; // for debugging
    // opacity: 1;
    li {
        position: relative;
        white-space: normal;
        color: @color-resolution-blue;
        border-bottom: 1px solid @color-gray-light;
        span {
            position: absolute;
            display: block;
            right: 20px;
            bottom: 10px;
            font-weight: @weight-normal;
            font-size: 1.4rem;
            color: @color-gray;
        }
        &:hover {
            color: @color-white;
            background-color: @color-cerulean;
        }
    }
    a {
        font-weight: @weight-bold;
    }
}

.svc-cb-menu__sb {
    // z-index: @z-index-mid; // for debugging
    // opacity: 1;
    li {
        font-weight: @weight-bold;
        color: @color-resolution-blue;
        border-bottom: 1px solid @color-white-dark;
        &:hover {
            color: @color-white;
            background-color: @color-cerulean;
        }
    }
}
