.site__menu-main {
    display: flex;
    justify-content: space-between;
    font-weight: @weight-semibold;
    width: 100%;
    max-width: 930px; 
    margin-right: 2%;
}

.smm-submenu {
    ul {
        position: absolute;
        z-index: @z-index-high;
//         columns: 5;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;

        transform: scaleY(0);
        transform-origin: top;
        transition: transform 0.2s;

        top: 100%;
        left: 0;
        width: 100%;
        padding: 20px 10%;
        font-size: 1.4rem;
        border-top: 3px solid @color-resolution-blue;
        background-color: @color-cerulean;
        box-shadow: 0 3px 10px @color-gray;
        // border-top: 3px solid @color-resolution-blue;
        a {
        	display: block;
        	padding: 4px 0;
            color: @color-white;
        }
    }

    // Temporary switch on, before JS is written.
    // &:hover {
    //     ul {
    //         transform: scaleY(1);
    //     }
    // }

}

.smm-submenu--international {
    ul {
        display: block;
        columns: 5;
    }
}

.smm-submenu-toggler {
	display: block;
	position: relative;
	.wj-icon-cm-arrow-down {
		position: absolute;
		top: -50%;
		left: 94%;
		font-size: 1.8em;
	}
    &.wj-expanded {
        +ul {
            transform: scaleY(1);
        }
    }
}
