.blog-page-content {
    background-color: @color-white-dark; // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(35, @screen-base-height);
    padding-top: @height-in-rem; // fallback for older browsers
    padding-top: @height-in-vh;
    padding-bottom: @height-in-rem; // fallback for older browsers
    padding-bottom: @height-in-vh;
    h3 {
        display: block;
        padding: 15px 20px 10px 20px; // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(30, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        font-weight: @weight-bold;
        color: @color-resolution-blue;
        background-color: @color-white;
    }
}

.bpc-posts-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
        max-width: 380px;
        flex: 0 1 32%; // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        background-color: @color-white;
        transition: all 0.3s;
        &:hover {
            box-shadow: 0 3px 20px @color-gray;
            img {
                transform: scale(1.1);
            }
        }
    }
    &__text {
        padding: 15px;
        h5 {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(10, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
            font-weight: @weight-bold;
            color: @color-resolution-blue;
        }
        p {
            // converts px into vh: get the result in @height-in-vh
            .wj-pixeltovh(15, @screen-base-height);
            margin-bottom: @height-in-rem; // fallback for older browsers
            margin-bottom: @height-in-vh;
            line-height: 1.4;
        }
    }
    .bpc-posts-list__img-wrapper {
        overflow: hidden;
    }
    img {
        display: block;
        transition: transform 0.3s;
    }
}
