// Common non-critical form elements
.wfsg-spinner {
    display: inline-block; 
    position: relative;
    height: 30px;
    line-height: 1;
    vertical-align: middle;
    background-color: @color-transparent;
    visibility: hidden;
    opacity: 0;
    width: 0;
    margin-left: 10px;
    transition: opacity 0.3s, width 0.3s;
    svg {
        width: 100%;
        height: 100%;
    }
}

.wj-modal-close {
    .default-form-close();
    font-size: 3rem;
    line-height: 0;
}

// Helping mixins
.default-form-close() {
    position: absolute;
    right: 0;
    top: 0;
    width: 25px;
    height: 25px;
    border: 0;
    padding: 3px 0 0 0;
    background-color: @color-transparent;
    &:hover {
        background-color: @color-transparent;
    }
    .wj-icon {
        &:before {
            .center-absolute-hv();
        }
    }
}

.wj-popup-loading-state {
    display: block;
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.4); // visibility: hidden; // Set up in _popup-cleanup.less
    // opacity: 0;
    transition: all 0.3s;
    &:after {
        content: "";
        position: fixed;
        left: 50%;
        top: 50%;
        width: 50px;
        height: 50px;
        animation: spin1 1s linear infinite;
    }
    &.wj-loading {
        visibility: visible;
        opacity: 1;
    }
}
