@media screen and (max-width: 980px) {
    .wj-main-container {
        max-width: 980px;
        width: 100%;
    }
    .header-wrapper__topline {
        display: none;
        font-size: 1.6rem;
    }
    .header-wrapper__brandline {
        padding-top: 20px;
        margin-bottom: 0;
    }
    .hwb-logo {
        position: relative;
        &:after {
            content: "";
            position: absolute;
            height: 100%;
            width: 53%;
            right: 0;
            background-color: @color-white;
        }
    }

    .hwb-responsive-block {
        display: block;
    }

}

@media screen and (max-width: 980px) {
    .form-vacancy-search {
        h4 {
            padding-left: 0;
            text-transform: uppercase;
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom: 3px solid @color-white;
            .wj-icon-cm-search {
                display: none;
            }
        }
    }
}
