// ------ Icon helpers
[class*="wj-icon-cm"] {
    &:before {
        font-weight: @weight-normal;
        font-family: @font-icons-common !important;
        font-style: normal;
        font-size: inherit;
        vertical-align: middle;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        speak: none;
        /* Better Font Rendering =========== */
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

