.scv-search-results__not-found {
    padding: 20px;
    background-color: @color-white;
}

.scv-sr-nf__message {
    padding: 0 0 40px 20px;
    line-height: 1.6;
    border-bottom: 1px solid @color-cerulean;
    margin-bottom: 40px;
    >p {
        margin-bottom: 15px;
    }
    ul {
        padding-left: 60px;
        list-style-type: square;
    }
}

.scv-sr-nf__contact-hays {
    text-align: center;
    margin-bottom: 50px;
    h4 {
        color: @color-resolution-blue;
    }
    ul {
        display: flex;
        justify-content: space-around;
        li {
            flex: 0 1 300px;
            >p {
                margin: 20px auto;
            }
        }
    }
}

.scv-sr-nf__directions {
	padding: 20px;
    background-color: @color-resolution-blue;
    h5 {
    	padding-bottom: 10px;
    	margin-bottom: 15px;
    	color: @color-white;
    	border-bottom: 1px solid @color-white;
    }
    ul {
        columns: 4;
    }
    a {
    	color: @color-white;
    }
    li {
    	margin-bottom: 15px;
    }
}
