// Site start loader
// 01.12.2017 11:12:19
.wj-loader {
    &:after {
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: cover;
    }
}

body.wj-loading {
    overflow: hidden;
    &:after {
        content: "";
        display: block;
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: cover;
        width: 50px;
        height: 50px;
        opacity: 1;
        position: fixed;
        left: 50%;
        top: 50%;
        animation: spin1 1s linear infinite;
    }
}

#jump-site-top.wj-loading {
    opacity: 0;
    overflow: hidden;
}

@keyframes spin1 {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}
