// Init invisible state for popups at inlined CSS

/* --- Universal --- */

// --- Universal popup, Site popup mount
.wj-popup-loading-state,
.site-popup-mount {
    visibility: hidden;
    opacity: 0;
}

// --- Server popup message
[class*="rhy-serv-messages__msg-"] {
    visibility: hidden;
    opacity: 0;
}


/* --- Site-dependent --- */

.site-info-popup, .site-popup-form .wj-form-wrapper {
    visibility: hidden;
    opacity: 0;
}



