.site-header {
    background-color: @color-white;
}

.header-wrapper {
    position: relative;
}

.header-wrapper__topline {
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid @color-white-dark;
    font-size: 1.4rem; // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(45, @screen-base-height);
    margin-bottom: @height-in-rem; // fallback for older browsers
    margin-bottom: @height-in-vh;

    >li {
        padding: 5px 15px;
//         margin: 5px 0;
        border-left: 1px solid @color-white-dark;
    }
    a {
        color: @color-resolution-blue;
    }
    .hwt-social {
        display: none;
    }
}

.header-wrapper__brandline {
    position: relative;
    display: flex;
    justify-content: space-between; // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(30, @screen-base-height);
    margin-bottom: @height-in-rem; // fallback for older browsers
    margin-bottom: @height-in-vh;
}

.hwb-responsive-block {
    display: none;
    position: relative;
    top: -9px;
    color: @color-gray-light;
    width: 120px;
    .wj-icon-cm-search {
        margin-right: 25px;
        font-size: 3.8rem;
        &:hover {
            color: @color-gray;
        }
    }
}

.wj-menu-line-floating-wrapper {
    height: 50px;
}

.header-wrapper__menuline {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    z-index: 10;
}

.header-wrapper__menuline--floating {
    position: fixed;
    width: 100%;
    max-width: @screen-project-default;
    background-color: white;
    left: 50%;
    top: 0;
    padding: 20px;
    box-shadow: 0 3px 10px grey;
    transform: translateX(-50%);
    opacity: 0;
    // transition: transform 0.3s, opacity 0.3s;
    transition: opacity 0.3s;
    z-index: @z-index-minus;
    &.wj-visible {
        // transform: translate(-50%, 0);
        z-index: @z-index-highest;
        opacity: 1;
    }
}

.wj-burger-icon {
    color: @color-gray-light;
    background-color: @color-transparent;
    &:hover {
        color: @color-gray;
    }
    &:after {
        content: "\2261";
        font-size: 4rem;
    }
}

.hwt-lang-selector {}

@import url("blocks/site-search.less");
@import url("blocks/site-menu-main.less");
// @import url("blocks/site-menu-applicants.less");
@import url("blocks/site-social.less");
