// !!!: CHECK FIRST THING AFTER FONTS ARE SETUP
// in the debugger that they load with no errors
// generaly the path should be '../fonts/'
// and that all file names in !!! url() LONG LINES !!!have correct path
// !!!: In case some fonts are inlined in CSS as base64,
// do not mix them with the fonts loading from disk
// in the same @font-face declaration.
// 
// To async load fonts with my FontFaceObserver fontLoad()
// helper separate these between different @font-face
// declaration.
// --- my own on-demand generated default font for icon
// and symbols, gathered for a particluar project
// 
// !!!: HINT FOR ICOMOON FONT: If no icons are visible, load the same font from .ttf or .woff file
// and then check if the transfonter.org conversion to base64 was correct (latin, all)

@font-face {
    font-family: 'icomoon';
    src: url('../fonts/icons.woff2') format('woff2'),
    url('../fonts/icons.woff') format('woff'),
    url('../fonts/icons.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Gotham Narrow A";
