.search-page-content {
    background-color: @color-white; // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(50, @screen-base-height);
    padding-bottom: @height-in-rem; // fallback for older browsers
    padding-bottom: @height-in-vh;
    border-top: 3px solid @color-gray-light;
}

.spc-form-vacancy-search {
    // converts px into vh: get the result in @height-in-vh
//     .wj-pixeltovh(20, @screen-base-height);
//     padding-bottom: @height-in-rem; // fallback for older browsers
//     padding-bottom: @height-in-vh;
    border-bottom: 3px solid @color-white-dark;
    background-color: lighten(@color-gray-light, 7%);
}

.search-controller-view {
    background-color: @color-white-dark;
    padding: 20px;
}


.scv-content {
    display: flex;
    justify-content: space-between;
}

.scv-sidebar {
    flex: 0 1 255px;
    min-width: 255px;
    margin-right: 20px;
}

.scv-search-results {
    flex: 0 1 906px;
}

.svc-search-results__item {
    display: flex;
    justify-content: space-between;
//     flex-wrap: wrap;
    align-items: flex-start;
    padding: 20px;
    margin-bottom: 20px;
    background-color: @color-white;
    box-shadow: 0 3px 5px @color-gray-light;
    .wj-btn-standard {
//         padding: 9px 15px;
        padding-left: 20px;
        padding-right: 20px;
    }
}

.svc-search-results__position {
    font-size: 1.4rem;
    margin-right: 2%;
    h3 {
//         font-weight: @weight-medium;
        margin-bottom: 10px;
        font-size: 2.2rem;
        a {
            color: @color-resolution-blue;
        }
    }
}

.svc-search-results__details {
    [class*="wj-icon-cm"] {
        display: inline-block;
        margin: 0 10px 10px 0;
        &:before {
            border-radius: 50%;
            font-size: 1.2em;
            padding: 1px;
            color: @color-white;
            background-color: @default-color-text;
        }
    }
}

@import url("partials/search-control-bar.less");
@import url("partials/search-sidebar-widget.less");
@import url("partials/search-not-found.less");
