.main-page-content {
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: @color-white-dark;
}

.main-page-tiles {
    ul {
        display: flex;
        justify-content: space-between;
        li {
            display: block;
            padding: 15px;
            background-color: @color-white;
            max-width: 380px;
            h2 {
                display: block;
                position: absolute;
                z-index: @z-index-mid;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: @color-cerulean;
                font-weight: @weight-bold;
                text-transform: uppercase;
                white-space: nowrap;
                text-shadow: 1px 1px 0px @color-resolution-blue;
            }
            h5 {
                color: @color-resolution-blue;
                font-weight: @weight-bold; // converts px into vh: get the result in @height-in-vh
                .wj-pixeltovh(30, @screen-base-height);
                margin-bottom: @height-in-rem; // fallback for older browsers
                margin-bottom: @height-in-vh;
            }
        }
    }
}

.mpt-tile {
    position: relative;
    display: block;
    overflow: hidden;
//     border: 5px solid @color-cerulean;
    margin: -15px -15px 20px -15px;
    img {
        display: block;
        transition: transform 0.3s;
    }
    &:after {
//         content: "";
        position: absolute;
        z-index: @z-index-normal;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: fade(@color-white, 60%);
    }
    &:hover {
        img {
            transform: scale(1.1)
        }
    }
}
