  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: cover;
}
body.wj-loading {
  overflow: hidden;
}
body.wj-loading:after {
  content: "";
  display: block;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  width: 50px;
  height: 50px;
  opacity: 1;
  position: fixed;
  left: 50%;
  top: 50%;
  animation: spin1 1s linear infinite;
}
