.bpc-resume-wrapper {
    background-color: @color-white;
    .form-common-on-page {
        padding: 20px;
    }
}

.fsr-attach-resume {
    margin-bottom: 20px;
    >p {
        color: @color-cerulean;
        margin-bottom: 20px;
    }
    ul {
        display: flex;
        justify-content: space-between; // align-items: stretch;
        flex-wrap: wrap;
        li {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 30px;
            text-align: center;
            flex: 1 1 25%;
            border: 1px solid @color-gray-light;
            +li {
                border-left: 0;
            }
            a {
                min-width: 120px;
            }
            img {
                display: block;
                margin: 0 auto;
                width: auto;
                height: 50px;
            }
        }
    }
    .wj-form__field--file {
        margin-bottom: 0;
        input {
            position: absolute;
            top: 0;
            left: 0;
            width: 0.1px;
            height: 0.1px;
            opacity: 0;
            overflow: hidden;
            z-index: -1;
        }
    }
    .wj-icon-cm-check {
        // display: none;
        position: absolute;
        top: 18%;
        left: 64%;
        font-size: 4rem;
        color: @color-cerulean;
        // color: @color-gray;
    }
}

.wj-form__field--applicant-accepted {
    input {
        display: inline-block;
        width: auto !important;
    }
    label {
        font-size: 1.4rem;
        color: @default-color-text;
        a {
            display: initial;
        }
    }
}
