// Redefine page-specific styles for some elements 
// Main page specific styling
.main-vacancy-search {
    .form-vacancy-search {
        background-color: fade(@color-cerulean, 60%);
    }
    .wj-btn-standard {
        background-color: @color-gold-drop;
    }
    .wj-form__field {
        label {
            color: @color-white;
        }
    }
}


// Define the common styles for the vacancy search form
.form-vacancy-search {
    h4 {
        position: relative;
        color: @color-white;
        font-weight: @weight-bold;
        padding-left: 40px;
        margin-bottom: 20px;
        font-size: 2.4rem;
        .wj-icon-cm-search {
            position: absolute;
            left: -10px;
            top: -11px;
            font-size: 2em;
        }
    }
    .wj-form {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
    .wj-form-data-fields {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-width: 620px;
    }

    .wj-form__submit-group {
        .wfsg-spinner {
            display: none;
        }
    }
    .wj-btn-standard {
        padding: 11px 40px 11px 40px;
        white-space: nowrap;
        &:hover {
            background-color: @color-resolution-blue;
        }
        .wj-icon-cm-search {
            position: absolute;
            left: 12px;
            top: 5px;
            font-size: 1.6em;
        }
    }
}

// Search page specific styling
.spc-form-vacancy-search {
    .wj-form-wrapper {
        margin: auto;
        background-color: @color-transparent;
        max-width: 990px;
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
    .wj-form__field {
        label {
            display: none;
        }
        [class*="wj-icon-cm-"] {
            position: absolute;
            left: 5px;
            top: 9px;
            font-size: 1.6em;
        }
        input {
            padding: 12px 13px 12px 30px;
            width: 100vw;
            max-width: 380px;
        }
    }
    .wj-btn-standard {
        background-color: @color-resolution-blue;
        &:hover {
            background-color: @color-cerulean;
        }
    }
    .wj-form-data-fields {
        max-width: 780px;
    }
}
