 */

.wj-form-wrapper {
    position: relative;
    z-index: @default-form-zi;
    font-size: @default-form-text;
    line-height: @default-form-text * 1.3;
    color: @default-form-txtc;
    background-color: @default-form-bgc;
    width: @default-form-w;
    max-width: @default-form-maxw;
    padding: @default-form-padding;
    min-width: 290px;
}

.wj-form {
    input,
    label {
        display: block;
    }
    input {
        .default-input-style();
        width: 100%;
        &[type="tel"] {
            width: 100%;
        }
    }
    select {
        .default-input-style();
        font-size: 1.4rem;
        border-bottom: none;
        width: 100%;
    }
    textarea {
        .default-input-style();
        width: 100%;
        resize: none;
    }
}

.wj-form__field {
    position: relative; // converts px into vh: get the result in @height-in-vh
    .wj-pixeltovh(15, @screen-base-height);
    margin-bottom: @height-in-rem; // fallback for older browsers
    margin-bottom: @height-in-vh;
}

.wj-form__submit-group {
    &.wj-issending {
        .wfsg-spinner {
            visibility: visible;
            opacity: 1;
            width: 30px;
        }
        .wfsg-send {
            pointer-events: none; 
        }
    }
}

.default-input-style() {
    color: @default-form-inp-txtc;
    background-color: @default-form-inp-bgc;
    border: @default-form-inp-bdr;
}
