.cpc-body-item {
    .cpc-widget {
        padding: 0;
    }
}

.cpc-widget {
    padding: 20px;
    background-color: @color-white;
    h4,
    h6 {
        color: @color-resolution-blue; // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
    }
    p {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(15, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        line-height: 1.4;
    }
    a {
        font-weight: @weight-medium;
    }
    +.cpc-widget {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        margin-top: @height-in-rem; // fallback for older browsers
        margin-top: @height-in-vh;
    }
    &__footer {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(25, @screen-base-height);
        margin-top: @height-in-rem; // fallback for older browsers
        margin-top: @height-in-vh;
        padding-top: @height-in-rem; // fallback for older browsers
        padding-top: @height-in-vh;
        font-size: 1.4rem;
        border-top: 1px solid @color-gray-light;
        .wj-btn-standard {
            text-align: center;
            width: 100%;
        }
    }
    .site-social-block ul li:first-of-type {
        margin-left: 0;
    }
    hr {
        border-style: none;
        border-bottom: 1px solid @color-white-dark;
    }
}

.cpc-widget--practices {
    .wj-toggler {
        position: relative;
        margin-bottom: 0;
        a {
            color: @color-resolution-blue;
        }
        [class*="wj-icon-cm-"] {
            position: absolute;
            top: 0;
            right: 0;
        }
        .wj-icon-cm-plus {
            display: block;
        }
        + ul {
            display: none;
        }
        &.wj-expanded {
            margin-bottom: 20px;
            .wj-icon-cm-plus {
                display: none;
            }
            .wj-icon-cm-minus {
                display: block;
            }
            + ul {
                display: block;
            }
        }
    }
    li {
        padding: 10px 0;
        border-bottom: 1px solid @color-white-dark;
    }
    a {
        font-weight: @weight-normal;
    }
}

@import url("../blocks/form-contact-us.less");
