.site-social-block {
    max-width: 200px;
    ul {
        display: flex;
        justify-content: flex-start;
        li {
            &:not(:first-of-type) {

            
            margin-left: 7px;
            }
            position: relative;
            width: 30px;
            height: 30px;
            background-color: @color-cerulean;
            transition: background-color 0.3s;
            
            &:hover {
                background-color: @color-resolution-blue;
            }
            a {
            	display: block;
            	width: 100%;
            	height: 100%;
                color: @color-white;
                font-size: 17px;
                &:before {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            .wj-icon-cm-yt {
                font-size: 17px;
            }
            .wj-icon-cm-insta {
                font-size: 17px;
            }
            .wj-icon-cm-lin {
                font-size: 17px;
            }            
            .wj-icon-cm-email {
                font-size: 19px;
            }
        }
    }
}
