.form-contact-us {
	padding: 0;
	min-width: auto;
	max-width: none;
	width: 100%;
	background-color: @color-transparent;
	.wj-form__field {
		width: 100%;
		margin-bottom: 10px;
		input {
			width: 100%;
			padding: 8px 10px;
			border: 0;
			background-color: @color-white-dark;
		}
	}
	.wj-btn-standard {
		width: 100%;
		padding-left: 10px;
		padding-right: 10px;
	}
	.wfsg-spinner {
		display: none;
	}
}