@media screen and (max-width: 980px) {
    .main-banner__bg {
        display: none;
    }
    .main-banner {
        padding-top: 0;
        padding-bottom: 0;
        h1 {
            display: none;
        }
    }
    .main-banner__controls {
        margin-left: -20px;
        margin-right: -20px;
        background-color: @color-cerulean;
    }
    .main-vacancy-search {
        z-index: @z-index-normal;
        width: 100%;
        margin-bottom: 55px;
//         background-color: @color-cerulean;
        .form-vacancy-search {
            width: inherit;
            max-width: none;
            padding: 40px 20px 35px 20px;
            background-color: inherit;
        }
    }
    .form-vacancy-search {
        .wj-form,
        .wj-form-data-fields {
            display: block;
        }
        .wj-form-data-fields {
            max-width: none;
        }
        .wj-form__field {
            margin-bottom: 20px;
            input {
                width: 100%;
            }
        }
    }
    .site-menu-directions-wrapper {
        position: absolute;
        z-index: @z-index-ground;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        &.wj-expanded {
            z-index: @z-index-mid;
        }
    }
    .smd-toggler {
        left: 20px;
        bottom: -84%;
        background-color: @color-resolution-blue;
        &:hover {
            background-color: lighten(@color-resolution-blue, 20%);
        }
    }
    .site-menu-directions__list-wrapper {
        height: 100%;
        overflow: auto;
        padding-bottom: inherit;
    }
}

@media screen and (max-width: 740px) {
    .site-menu-directions ul {
        columns: 3;
    }
}

@media screen and (max-width: 580px) {
    .site-menu-directions ul {
        columns: 2;
    }
}

@media screen and (max-width: 380px) {
    .site-menu-directions ul {
        columns: 1;
    }
}
