@media screen and (max-width: 980px) {
    .wj-mobile {
        margin-left: 0;
        transition: all ease 0.3s;
        &.wj-expanded {
            overflow: visible;
            margin-left: -240px;
        }
        .header-wrapper__topline {
            right: -240px;
        }
        .wj-menu-line-floating-wrapper {
            right: -240px;
        }
    }
    .header-wrapper__topline {
        position: absolute;
        display: initial;
        top: 0; // right: 0;
        width: 240px; //         padding-top: 20px;
        background-color: white;
        border-left: 1px solid @color-white-dark; // transition: right 0.3s;
        >li {
            padding: 13px 15px;
            border-bottom: 1px solid @color-white-dark;
        }
        .hwt-social {
            display: block;
        }
        .site-social-block {
            display: block;
            padding-left: 0;
            padding-top: 10px;
        }
    }
    .wj-menu-line-floating-wrapper {
        position: absolute;
        top: 158px; // right: 0;
        width: 240px; // transition: right 0.3s;
    }
    .header-wrapper__menuline {
        flex-direction: column;
        padding-bottom: 0;
        border-left: 1px solid @color-gray-light;
        background-color: @color-white;
        .site-social-block {
            display: none;
        }
    }
    .site__menu-main {
        flex-direction: column;
        padding: 0;
        li {
            padding: 15px 20px;
            border-bottom: 1px solid @color-gray-light;
        }
    }
    .smm-submenu {
        position: relative;
        ul {
            flex-direction: column;
            left: auto;
            right: -2px;
        }
    }
    .smm-submenu--international {
        ul {
            display: flex;
        }
    }
    .site-social-block {
        margin-left: 0;
        padding-top: 15px;
        margin-right: auto;
        padding-left: 10px;
    } // Mobile search block open / close.
    .hwb-search {
        display: none;
    }
    .wj-brand-line-floating-wrapper {
        min-height: 66px;
    }
    .header-wrapper__brandline {
        &.wj-expanded {
            padding-bottom: 40px;
            .hwb-search {
                display: block;
                position: absolute;
                top: 61px;

                left: 0;
                width: 100%;

                .header-search {
                    input {
                        width: 100%;
                        max-width: none;
                    }
                }
            }
            &.wj-floated {}
        }
        &.wj-floating {
            z-index: @z-index-highest;
            position: fixed;
            left: 0;
            padding-left: 20px;
            padding-right: 20px;
            background-color: @color-white;
            box-shadow: 0 3px 10px @color-gray;

            .hwb-search {
                padding-left: 20px;
                padding-right: 20px;
            }
        }
    }
}
