.page-register-vacancy {
    background-color: lighten(@color-gray-light, 7%);
    .spc-form-vacancy-search {
        border-bottom: 0;
    }
    .form-common-on-page {
        padding: 0;
        input,
        textarea,
        select {
            border: 1px solid @color-gray-light;
        }
        select,
        textarea {
            padding: 11px 13px 12px 13px;
        }
        textarea {
        	resize: vertical;
        }
    }
}

.register-vacancy-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 990px;
    margin: 0 auto;
}

.register-vacancy {
    flex: 1 1 690px;
    max-width: 690px;
    padding: 20px;
    margin-right: 20px;
    background-color: @color-white;
    h5 {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(10, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        font-weight: @weight-bold;
        color: @color-cerulean;
        +p {
            font-size: 1.4rem;
        }
    }
}
