/* --- debug-border --- */

.generate-debug-border(@border-state: off) {
    .test-border(@border-state);
    .test-border (@border-state) when (@border-state =off) {
        border-style: none;
    }
    .test-border (@border-state) when (@border-state =on) {
        border: 1px solid red;
    }
}

// mixin to ease including debug border in CSS inline
.debug-border(@db-color: red; @db-line: solid; @db-width: 1px) {
    border: @db-width @db-line @db-color;
    // border: 1px dashed red;
}

