.scv-sidebar-widget {
    padding: 20px 15px;
    background-color: @color-white;
    margin-bottom: 20px;
    h6 {
        position: relative;
        display: block;
        width: 100%;
        padding-bottom: 20px;
        font-size: 1.4rem;
        color: @color-resolution-blue;
        span {
            display: block;
        }
        a {
            color: inherit;
        }
        [class*="wj-icon-cm-"] {
            position: absolute;
            top: -8px;
            right: 0;
            font-size: 1.8em;
        }
        &.wj-open {
            .wj-icon-cm-plus {
                display: none;
            }
        }
    }
}

        input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: @color-gray-light;
    opacity: 1; /* Firefox */
}


.widget-city-filter {
    position: relative;
    margin: 0 -15px 15px -15px;
    padding: 10px 15px;
    border-top: 2px solid @color-white-dark;
    border-bottom: 2px solid @color-white-dark;
    color: @color-gray-light;
    input {
        font-size: 1.4rem;
        border: none;

    }
    span {
        position: absolute;
        top: 5px;
        right: 0;
        font-size: 1.7em;
    }
}

.scv-sidebar-widget-body__data {
    font-size: 1.4rem;
    margin-bottom: 10px;
    li {
        display: flex;
        justify-content: space-between; // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(15, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        span {
            color: @color-gray;
        }
    }
    a {
        color: @color-resolution-blue;
    }
}

.scv-sidebar-widget-body__show-toggler {
    font-size: 1.2rem;
    font-weight: @weight-medium;
    font-style: @style-italic;
    color: @color-cerulean;
    span {
        &:first-of-type {
            display: none;
        }
        &:last-of-type {
            display: initial;
        }
    }
    &.wj-open {
        span {
            &:first-of-type {
                display: initial;
            }
            &:last-of-type {
                display: none;
            }
        }
    }
}
