@media screen and (max-width: 980px) {
    @viewport-width: 980px;
    .main-page-tiles {
        ul {
            flex-direction: column;
            li {
                max-width: 100%;
                h2 {
                    // convert font-size in rem into vw: get the result in @size-in-vw
                    .wj-remtovw(7, @viewport-width);
                    font-size: @size-in-vw;
                }
            }
        }
    }
    .mpt-tile {
        border-width: 10px;
        img {
            width: 100%;
        }
    }
    .mpt-tile-bg {
        display: block;
        max-height: 320px;
        overflow: hidden;
    }
}

@media screen and (max-width: 480px) {
    .mpt-tile {
        border-width: 5px;
    }
}
