.header-search {
    input {
        width: 100vw;
        max-width: 280px;
        font-size: 1.4rem;
        padding: 8px 30px 8px 10px;
        border: none;
        background-color: @color-white-dark;
    }
}

.header-search {
    position: relative;
    .wfsg-spinner {
        display: none;
    }
}

.wj-btn-search {
    position: absolute;
    font-size: 2em;
    top: 0;
    right: 0;
    height: 100%;
    color: @default-color-text;
    background-color: @color-transparent;
    .wj-icon-cm-search {
        &::before {
            position: absolute;
            right: 0;
        }
    }
}
