.footer-content {
    background-color: @color-white;
    padding-bottom: 50px;
}

.footer-intro {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    margin-bottom: 20px;
    h6 {
        display: block;
        padding-bottom: 5px;
        margin-bottom: 15px;
        font-size: 1.6rem;
        color: @color-resolution-blue;
        border-bottom: 1px solid @color-gray-light;
    }
    li {
        font-size: 1.4rem;
        font-weight: @weight-bold;
        margin-bottom: 8px;
        [class*="wj-icon"] {
        	&:before {
        		margin-right: 15px;
        	}
        }
    }
    &-item {
        width: 100%;
        max-width: 285px;
    }
}

.footer-menu {
    padding-top: 10px;
    margin-bottom: 15px;
    border-top: 1px solid @color-gray-light;
    ul {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
    }
    li {
        padding: 3px 10px;
        font-size: 12px;
        white-space: nowrap;
        border-left: 1px solid @color-gray-light;
        a {
            color: @default-color-text;
        }
        &:first-of-type {
            padding-left: 0;
            border-left: none;
        }
    }
}

.footer-copyr {
	display: block;
	font-size: 12px;
	margin-bottom: 15px;
}

.site-address {
	font-size: 12px;
	line-height: 18 / 14;
}