// !!!: fonts now in _fonts.less
// ------ General settings
html {
    font-size: 62.5%;
    box-sizing: border-box; // text-size-adjust: none;
    // -moz-text-size-adjust: none;
    // -webkit-text-size-adjust: none;
    // -ms-text-size-adjust: none;
    // overflow-x: hidden;
    // width: 100vw;
}

* {
    box-sizing: border-box;
}

// fallback before fontfaceobserver loads fonts (see below)
body {
    font-size: @text-size-base;
    font-weight: @weight-normal;
    font-style: @style-normal;
    font-family: @font-main, sans-serif;
    position: relative;
    box-sizing: border-box;
    color: @default-color-text;
    background-color: @default-color-background;
    transition: opacity 0.15s;
}

body.rhy-atf-loaded {
    opacity: 1;
    overflow: initial;
}

// fonts loading technique from Filament Group & fontfaceobserver
// https://www.filamentgroup.com/lab/font-events.html
// https://github.com/bramstein/fontfaceobserver
// see https://fontfaceobserver.com/
.fonts-loaded body,
input,
textarea,
button,
address {
    font-family: @font-main, sans-serif;
    font-size: @text-size-base;
    font-weight: @weight-normal;
    font-style: @style-normal;
    color: @default-color-text; // background-color: @default-color-background;
}

// clear blue outline on input:focus fields;
input,
button,
textarea {
    &:focus {
        outline: initial;
    }
}


/* ------ Cross-browser init section */

// --- Firefox
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    .clear-webkit-props(); // clear <p> unwanted margin-top and margin-bottom
    margin-top: 0;
    margin-bottom: 0;
}

ul {
    list-style: none;
}

// reset <a> links
a {
    color: @default-link-color;
    transition: color 0.5s ease;
    transition: background-color 0.5s ease;
    text-decoration: none;
    &:hover,
    &:active,
    &:focus {
        cursor: pointer;
        color: @default-link-hover-color;
        background-color: @default-link-bg-hover-color;
    }
    &:focus {
        outline: transparent;
    }
}

button,
select {
    &:hover {
        cursor: pointer;
    }
}

button {
    border: 0;
    padding:0;
}

// Responsive images by default
img {
    max-width: 100%;
    height: auto;
}


/* Change autocomplete styles in WebKit */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    border: 0px solid @color-gray;
    -webkit-text-fill-color: inherit;
    -webkit-box-shadow: 0 0 0px 1000px #000000 inset;
    -webkit-box-shadow: 0 0 0px 1000px @color-gray-light inset;
    transition: background-color 5000s ease-in-out 0s;
}

// Clear paddin & other things for Firefox
ul,
ol {
    margin-block-end: 0;
    margin-block-start: 0;
    padding-inline-start: 0;
    padding: 0; // Clear IE invisible padding-left
}

// Clean-up fieldset 
fieldset,
dl,
dd {
    margin: 0;
    padding: 0;
    border: 0;
    .clear-webkit-props();
    -webkit-padding-before: 0;
    -webkit-padding-end: 0;
    -webkit-padding-after: 0;
}


/* Main selectors init */

.wj-main-container {
    //     border-top: 1px solid transparent;
    overflow: hidden;
    width: @screen-project-default-main-container;
    // max-width: @screen-project-default-main-container;
    min-width: @screen-project-default-minwidth;
    margin: 0 auto; // opacity: 0; // for site loading time, reset to 1 via sitestart.js 
}

.wj-container-fluid {
    position: relative;
    // border-top: 1px solid transparent;
}

.wj-container-fixed {
    max-width: @screen-project-default;
    position: relative;
    margin: 0 auto;
    padding: 0 20px;
    // border-top: 1px solid transparent;
}

// ------ Typography - headers etc
// header tags reset / initialize
h1 {
    color: @default-color-text;
    font-size: 4.6rem;
    font-weight: @weight-normal;
    line-height: 1.8;
}

h2 {
    color: @default-color-text;
    font-size: 3.9rem;
    font-weight: @weight-normal;
}

h3 {
    color: @default-color-text;
    font-size: 2.6rem;
    font-weight: @weight-normal;
}

h4 {
    color: @default-color-text;
    font-size: 2.2rem;
    font-weight: @weight-medium;
}

h5 {
    color: @default-color-text;
    font-size: 2rem;
    font-weight: @weight-normal;
}

h6 {
    color: @default-color-text;
    font-size: 1.8rem;
    font-weight: @weight-medium;
}

em {
    color: @color-gray;
    // font-weight: @weight-bold;
    font-style: normal;
}
