@media screen and (max-width: 980px) {
    .common-page-content {
        display: block;
    }
    .cpc-body {
        max-width: none;
        +.cpc-sidebar {
            margin-top: 20px;
            margin-left: 0;
        }
    }
    .cpc-sidebar {
        max-width: none;
    }
    .cpc-sidebar+.cpc-body {
        margin-left: 0;
        margin-top: 20px;
    }
    .bpc-two-cols {
        display: block;
        .cpc-body {
            +.cpc-body {
                margin-top: 20px;
            }
        }
    }
    .cpc-sidebar--collapsable {
        display: none;
    }
    .wj-contacts-tiles {
        display: block;
    }
    .register-vacancy-wrapper {
        display: block;
        .register-vacancy {
            max-width: none;
            width: 100%;
            margin-bottom: 20px;
        }
    }
}

@media screen and (max-width: 480px) {
    .cpc-body {
        table {
            margin-left: -20px;
            margin-right: -20px;
            font-size: 1.2rem;
        }
    }
    .cpc-body__float-img-wrapper {
        max-width: 140px;
    }
}
