// Standard buttons
.wj-btn-standard {
    display: inline-block;
    position: relative;
    font-size: @default-btn-fz;
    font-weight: @default-btn-fw;
    color: @default-btn-txtc;
    background-color: @default-btn-bgc;
    border: @default-btn-bdr;
    text-transform: @default-btn-ttransform;
    border-radius: @default-btn-bdradius;
    padding: @default-button-padding;
    transition: all 0.3s;
    &:hover,&:active,&:focus {
        color: @default-btn-txtc;
        background-color: @hover-button-bg-color;
    }
}

a.wj-btn-standard {
    &:hover {
        color: @default-btn-txtc;
    }
}

