.services-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    li {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(15, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
        margin-right: @height-in-rem; // fallback for older browsers
        margin-right: @height-in-vh;
        text-transform: uppercase;
//         max-width: 190px;
//         max-height: 190px;
        height: 190px;
        width: 190px;
        a {
        	position: relative;
            display: block; 
            height: 100%;
            padding: 15px 15px 50% 15px;
        }
        h5 {
        	color: @color-white;
            font-weight: @weight-bold;
        }
        [class*="wj-icon-cm-"] {
        	position: absolute;
        	bottom: -10%;
        	right: 0;
        	color: @color-white;
        	font-size: 10rem;
        }
    }
}

.wj-color-resolution-blue {
    background-color: @color-resolution-blue;
}

.wj-color-cerulean {
    background-color: @color-cerulean;
}

.wj-color-limeade {
    background-color: @color-limeade;
}

.wj-color-gold-drop {
    background-color: @color-gold-drop;
}

.wj-color-cerise {
    background-color: @color-cerise;
}

.wj-color-vivid-violet {
    background-color: @color-vivid-violet;
}
