@media screen and (max-width: 980px) {
    .spc-form-vacancy-search {
        padding-bottom: 0;
        .form-vacancy-search {
            width: 90%;
            padding-bottom: 0;
            .wj-form,
            .wj-form-data-fields {
                display: flex;
                align-items: flex-start;
            }
            .wj-form__field {
                width: 100%;
                margin-right: 2%;
            }
        }
    }

    .scv-control-bar__filters {
        .wj-btn-standard {
            padding-left: 2px;
        }
    }
    .scv-control-bar__filters-payment {
        .wj-btn-standard {
            padding-left: 33px;
        }
    }
    .scv-control-bar__reset-results {
        min-width: 190px;
        margin-right: 20px;
    }
    .scv-sidebar {
        flex-basis: 190px;
        min-width: 190px;
        margin-right: 20px;
    }
    .svc-search-results__details {
        min-width: 110px;
    }
    .scv-sr-nf__contact-hays {
        ul {
            display: block;
        }
    }
    .scv-sr-nf__directions {
        &-wrapper {
            max-height: 250px;
            height: 90vh;
            overflow: auto;
        }
        ul {
            columns: 2;
        }
    }
}

@media screen and (max-width: 760px) {
    .spc-form-vacancy-search {
        .form-vacancy-search {
            padding-bottom: 30px;
            .wj-form,
            .wj-form-data-fields {
                display: block;
            }
        }
        .wj-btn-standard {
            display: block;
            width: 100%;
            .wj-icon-cm-search {
                left: 50%;
                transform: translateX(-310%);
            }
        }
    }
    .form-vacancy-search .wj-form__field {
        &:last-of-type {
            margin-bottom: 10px;
        }
        input {
            max-width: none;
        }
    }
    .scv-control-bar__filters {
        .wj-btn-standard {
            text-align: left;
        }
    }
    .scv-control-bar__reset-results {
        padding-left: 45px;
        padding-right: 45px;
    }
    .scv-control-bar__filters-sort-by {
        .wj-btn-standard {
            padding-right: 10px;
        }
    }
    .scv-sidebar {
    	display: none;
    }
    .scv-control-bar {
    	display: block;
    }
    .scv-control-bar__reset-results {
    	width: 100%;
    	margin-bottom: 20px;
    }
}

@media screen and (max-width: 730px) {
    .svc-search-results__item {
        display: block;
    }
    .scv-control-bar__filters {
    	display: block;
    	background-color: @color-transparent;
    	li {
    		width: 100%;
    		margin-bottom: 20px;
    		.wj-btn-standard {
    			width: 100%;
    			padding-left: 35px;
    			&.wj-icon-cm-connection {
    				&:before {
    					display: none;
    				}
    			}
    		}
    	}
    }
    .svc-search-results__position,
    .svc-search-results__details {
        // converts px into vh: get the result in @height-in-vh
        .wj-pixeltovh(20, @screen-base-height);
        margin-bottom: @height-in-rem; // fallback for older browsers
        margin-bottom: @height-in-vh;
    }
    .svc-cb-fp {
    text-align: center !important;
    }


}

