.main-banner {
    position: relative; //     max-height: 354px;
    padding-top: 25px;
    padding-bottom: 25px;
    h1 {
        display: block;
        position: relative;
        z-index: @z-index-normal;
        font-size: 3.8rem;
        font-weight: @weight-bold;
        line-height: 1.2;
        max-width: 780px;
        padding: 20px 20px 15px 20px;
        color: @color-resolution-blue;
        background-color: @color-white;
        margin-bottom: 30px;
    }
}

.main-banner__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(@image-banner-main-bg);
    background-size: cover;
    background-position: 0 0;
}

.main-banner__controls {
//     position: relative; 
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.smd-toggler {

    &.wj-expanded {
        +.site-menu-directions {
            z-index: @z-index-high;
            opacity: 1;
        }
    }

    // Temporary on/off mechanism via hover for before JS stage.
    // &:hover {
    //     +.site-menu-directions {
    //         z-index: @z-index-mid;
    //         opacity: 1;
    //     }
    // }

}

.site-menu-directions {
    position: absolute;
    overflow: hidden;
    // z-index: @z-index-mid;
    z-index: @z-index-minus;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    padding: 15px 20px;

    color: @color-white;
    background-color: fade(@color-resolution-blue, 95%); // display: none;

    opacity: 0;
    transition: opacity 0.3s;

    // Temporar ON mechanism.
    // &:hover {
    //     z-index: @z-index-mid; 
    //     opacity: 1;
    // }

    h5 {
        color: inherit;
        font-weight: @weight-bold;
        border-bottom: 1px solid fade(@color-white, 30%);
        padding-bottom: 10px;
        margin-bottom: 20px;
    }
    ul {
        // display: flex;
        // justify-content: space-between;
        // flex-wrap: wrap;

        column-count: 4;

    }
    a {
        display: block;
        padding: 5px 0;
        font-size: 1.4rem;
        color: inherit;
        white-space: nowrap;
        &:before {
        	margin-right: 10px;
        }
    }
    .wj-modal-close {
    	top: 15px;
    	right: 15px;
    	color: inherit;
    }
}

@import url("blocks/form-vacancy-search.less");
